* {
  box-sizing: inherit; }
  *::before, *::after {
    box-sizing: inherit; }

html {
  box-sizing: border-box;
  height: 100%;
  overflow: hidden; }

body {
  height: 100%;
  line-height: 1;
  text-rendering: optimizeSpeed;
  text-decoration-skip: objects;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
  margin: 0;
  overflow: auto; }

p,
dd,
dl,
figure {
  margin: 0; }

blockquote {
  margin: 0;
  quotes: none; }

q {
  quotes: none; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

th {
  font-weight: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0; }

audio,
video {
  display: block; }

img {
  border: none; }

img,
svg {
  vertical-align: middle; }

iframe {
  border: none; }

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit; }

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit; }

abbr {
  border: none;
  text-decoration: none; }

b,
strong {
  font-weight: inherit; }

i,
em,
dfn {
  font-style: inherit; }

mark {
  background-color: transparent;
  color: inherit; }

small {
  font-size: inherit; }

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: inherit;
  line-height: 0; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  padding: 0;
  margin: 0; }

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none; }

button,
input {
  overflow: visible; }

button,
select {
  text-align: left;
  text-transform: none; }

input,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }
  input::-ms-expand,
  select::-ms-expand {
    display: none; }

button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  -webkit-appearance: none; }

textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

fieldset {
  padding: 0;
  margin: 0;
  border: none; }

legend {
  display: block;
  padding: 0;
  white-space: normal; }

:focus {
  outline: none; }

::placeholder {
  font-size: inherit;
  color: inherit;
  opacity: inherit; }

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0; }

button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none; }

[type='number']::-webkit-inner-spin-button, [type='number']::-webkit-outer-spin-button {
  height: auto; }

[type='search'] {
  outline: none; }
  [type='search']::-webkit-search-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; }

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

[hidden] {
  display: none; }

:disabled,
.disabled {
  cursor: not-allowed; }

::-ms-clear {
  display: none; }

:-webkit-autofill {
  box-shadow: 0 0 1000px #fff inset;
  -webkit-text-fill-color: currentColor; }

:root {
  --font-main: 'Poppins', sans-serif;
  --color-main: #000;
  --color-bg: #fff;
  --border: #000;
  --container: 1170px;
  --padding: 16px;
  --transition: .2s; }
  @media (min-width: 768px) {
    :root {
      --padding: 30px; } }
  @media (min-width: 992px) {
    :root {
      --padding: 60px; } }

body {
  color: var(--color-main);
  background-color: var(--color-bg);
  font-family: var(--font-main);
  font-size: 14px;
  font-weight: 400; }

strong {
  font-weight: 700; }

::selection {
  background-color: var(--color-main);
  color: var(--color-bg); }

.container {
  width: 100%;
  max-width: calc(var(--container) + var(--padding)*2);
  margin: 0 auto;
  padding-left: var(--padding);
  padding-right: var(--padding); }

.test {
  min-height: 100vh;
  padding-top: 50px;
  padding-bottom: 50px; }
  .test__grid {
    align-items: center; }
    @media (min-width: 992px) {
      .test__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 60px; } }
  .test__players-box {
    width: 100%;
    height: 400px;
    border: 2px solid var(--border); }
  .test__game {
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 20px 16px;
    border: 2px solid var(--border);
    border-radius: 80px;
    font-size: 18px; }
    @media (min-width: 992px) {
      .test__game {
        padding: 30px 50px;
        font-size: 24px; } }
    .test__game-item {
      width: 100%;
      text-align: center; }
      .test__game-item--content {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 16px; }
        @media (min-width: 992px) {
          .test__game-item--content {
            padding: 30px; } }
    .test__game-columns {
      display: flex;
      justify-content: space-between;
      margin-top: 40px; }
    .test__game-col {
      flex: 1;
      padding-top: 6px;
      padding-bottom: 6px;
      white-space: nowrap; }
      .test__game-col:not(:last-child) {
        border-right: 3px solid var(--border); }
    .test__game-content:not(.is-show) {
      display: none; }

.darkmode {
  --color-main: #fff;
  --color-bg: #000;
  --border: #fff; }

.switch-wrapper {
  position: relative;
  min-height: 34px;
  display: flex;
  align-items: center;
  margin-bottom: 30px; }

.switch {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 34px; }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0; }
    .switch input:checked + .change {
      background-color: #000;
      border: 2px solid #fff; }
      .switch input:checked + .change::before {
        transform: translateX(25px);
        background-color: #fff; }

.change {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  border: solid 2px #000;
  cursor: pointer;
  transition: var(--transition); }
  .change::before {
    content: '';
    position: absolute;
    height: 26px;
    width: 26px;
    left: 2px;
    bottom: 2px;
    background-color: #000;
    transition: var(--transition); }
  .change.round {
    border-radius: 34px; }
    .change.round::before {
      border-radius: 50%; }

.checkbox input[type='checkbox'] {
  height: 0;
  width: 0; }
  .checkbox input[type='checkbox'] + label {
    position: relative;
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 700;
    user-select: none;
    margin: .6em 0;
    cursor: pointer;
    transition: color 0.25s cubic-bezier(0.4, 0, 0.23, 1); }
    .checkbox input[type='checkbox'] + label span {
      position: relative;
      width: 1em;
      height: 1em;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      border: 1px solid var(--border);
      border-radius: 2px;
      margin-right: 1em;
      cursor: pointer;
      transition: all 0.25s cubic-bezier(0.4, 0, 0.23, 1); }
  .checkbox input[type='checkbox']:checked + label > span {
    border: .5em solid #FFEB3B;
    animation: shrink-bounce 0.2s cubic-bezier(0.4, 0, 0.23, 1); }
    .checkbox input[type='checkbox']:checked + label > span::before {
      box-sizing: content-box;
      content: '';
      position: absolute;
      top: -2px;
      left: -7px;
      border-right: 3px solid transparent;
      border-bottom: 3px solid transparent;
      transform: rotate(45deg);
      transform-origin: 0% 100%;
      animation: checkbox-check 0.125s 0.25s cubic-bezier(0.4, 0, 0.23, 1) forwards; }

@keyframes shrink-bounce {
  0% {
    transform: scale(1); }
  33% {
    transform: scale(0.85); }
  100% {
    transform: scale(1); } }

@keyframes checkbox-check {
  0% {
    width: 0;
    height: 0;
    border-color: #212121;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  33% {
    width: 5px;
    height: 0;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  100% {
    width: 5px;
    height: 12px;
    border-color: #212121;
    transform: translate3d(0, -0.5em, 0) rotate(45deg); } }

.btn {
  position: relative;
  width: 80%;
  height: 80px;
  display: block;
  margin: 0 auto;
  border: 1px solid var(--border);
  text-align: center;
  font-weight: 700;
  overflow: hidden;
  transition: color 0.2s ease-in-out; }
  @media (min-width: 992px) {
    .btn {
      width: 100%; } }
  .btn::before, .btn::after {
    z-index: -1;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-main);
    transition: transform 0.2s ease-in-out; }
  .btn::before {
    transform: translateY(-100%); }
  .btn::after {
    transform: translateY(100%); }
  .btn:hover {
    color: var(--color-bg); }
    .btn:hover::before {
      transform: translateY(-50%); }
    .btn:hover::after {
      transform: translateY(50%); }

.underline {
  position: relative; }
  .underline::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color-main);
    transform-origin: left;
    transform: rotateY(0);
    transition: transform 0.2s ease-in-out; }
  .underline:hover::after {
    transform-origin: right;
    transform: rotateY(90deg); }
